


import("./firebaseApp").then(app => {
  pushData = app.pushData;
  const firebaseStatus = window.document.getElementById(`firebaseStatus`) as HTMLDivElement;
  firebaseStatus.textContent = "載入完畢，連線至資料庫...";
});

let pushData = (a: string, b: string) => {
  return new Promise<boolean>(() => {
    alert("firebase 還未載入完畢。");
    return false;
  });
};

const startButton = window.document.getElementById("start") as HTMLButtonElement;
const logText = window.document.getElementById("logText") as HTMLTextAreaElement;
const placeText = window.document.getElementById("place") as HTMLSpanElement;
const testButton = window.document.getElementById("testButton") as HTMLButtonElement;
const buttonGroup = window.document.getElementById(`buttonGroup`) as HTMLDivElement;

const placeNames = ["健檢櫃台", "身高體重", "血壓脈搏", "心電檢查", "家醫診斷", "抽血檢查", "X光檢查", "牙醫檢查", "眼科檢查"];

placeNames.map((v, i, a) => {
  var button = document.createElement("button");
  button.textContent = v;
  button.className = "btn btn-success";
  button.onclick = () => {
    placeText.textContent = v;
  };
  buttonGroup.appendChild(button);
});

startButton.addEventListener("click", () => {
  if ("NDEFReader" in window) {
    const ndef = new NDEFReader();
    ndef
      .scan()
      .then(() => {
        log("Scan started successfully.");
        ndef.onreadingerror = () => {
          log("Cannot read data from the NFC tag. Try another one?");
        };
        ndef.onreading = (event) => {
          log("NDEF message read.");
          log(event.serialNumber);
          // log(event.message.records.map((v, i, a) => {JSON.stringify(v)}).join("\n"));
          pushData(event.serialNumber, placeText.textContent + "");
        };
      })
      .catch((error) => {
        log(`Error! Scan failed to start: ${error}.`);
      });
  } else {
    log("NDEFReader not in window");
  }
});

function log(message: string) {
  logText.value += message + "\n";
  logText.scrollTop = logText.scrollHeight - logText.clientHeight;
}


if (testButton) {
  testButton.onclick = async (ev) => {
    for (let i = 0; i < 100; i++) {
      const tempPlace = "place_" + Math.floor(Math.random() * 8);
      const success = await pushData("test" + (i % 80), tempPlace);
      if(!success) {
        return;
      } 
    }
  };
}


